.vertical-layout {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gdpr-intro {
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.base-panel .inner-container {
    max-width: 90vw;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.gdpr-intro .textImgs {
    width: 80%;
    height: auto;
    object-fit: contain;
    max-width: 500px;
    opacity: 1;
    position: relative;
}

.gdpr-intro h1 {
    max-width: 75%;
    margin: 5vh auto 10px;
    position: relative;
}

.gdpr-intro p {
    max-width: 80%;
    margin: 0 auto;
    font-size: 16px;
    position: relative;
}

div {
    width: auto;
    margin: auto 0;
}

svg {
    overflow: overlay;
    /* pointer-events: none; */
    cursor: pointer;
}

.cls-1 {
    fill: none;
    stroke: #bdf1f6;
    stroke-width: 1px;
    fill-rule: evenodd;
}

polygon {
    stroke: #7fff00;
    fill: none;
}

.cls-1 {
    fill: #fff;
}

