.permissions-intro {
    pointer-events: initial;
    touch-action: initial;
    text-align: center;
    background-color: #131826;
    font-family: "arial";
    height: 100%;
    color: #fff;
}

.vertical-layout {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.inner-container {
    max-width: 100vw;
    width: 95%;
    height: 100%;
}

.message-container {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.logo {
    margin: 20px 0 0 auto;
    width: 80px;
    height: 80px;
    position: relative;
}

.message-container * {
    position: relative;
}




* {
    -webkit-tap-highlight-color: transparent;
}