.App {
  text-align: center;
}

.container {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}


.base-panel {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #131826;
  pointer-events: initial;
  touch-actions: auto;
  text-align: center;
  background-color: #131826;
  justify-content: start;
  color: #fff;
}
